<template>
  <div id="app">
     
     <!-- <form action="https://yo08s2qyh9.execute-api.us-west-2.amazonaws.com/staging/?url=https://www.baidu.com&selectors[]=.mnav:nth-child(3)" method="post"> -->
        <button @click="getData">点击</button>
        <span>{{txt}}</span>
     <!-- </form> -->
    <router-view/>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "App",
  data(){
    return{
      txt:''
    }
  },
  methods: {
    // https://yo08s2qyh9.execute-api.us-west-2.amazonaws.com/staging/?url=https://www.baidu.com&selectors[]=.mnav:nth-child(3)
    getData: function(params) {
      // var data = {
      //   url: "https://baidu.com",
      //   query: ".mnav:nth-child(3)"
      // };

      // axios
      //   .post("/",
      //   {url:"https://www.baidu.com",
      //   selectors:'.mnav:nth-child(3)'
      //   })
      //   .then(resp => {
      //     console.log(resp);
      //   })
      //   .catch(err => {});


      if(!params){
        params={}
      }
      this.$api.get('staging',params,function(res){
        this.txt=res.data;
      })

    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



